<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>

    <!-- Content start -->
    <div class="row">
      <h3 class="col section-title">{{ $t('Automation')}}</h3>
    </div>
    <div class="row">
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
      <div class="col-md-4 mb-3">
        <div class="product-box"></div>
      </div>
    </div>
    <!-- Content end -->
  </div>
</template>
<script>
import { Circle4 } from 'vue-loading-spinner'

export default {
  name: 'Automation',
  components:{
    Circle4
  },
  props: {
    isChangeLanguge: Boolean
  },
  data(){
    return{
      isLoading: false
    }
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  },
  updated(){
    if (this.isChangeLanguge) {
      this.$store.commit('setLang', 'zh');
      this.$i18n.locale = 'zh';
      localStorage.setItem('footmark-lang', 'zh');
    } else {
      this.$store.commit('setLang', 'en');
      this.$i18n.locale = 'en';
      localStorage.setItem('footmark-lang', 'en');
    }
  }
}
</script>
